/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 2px solid;
  border-color: #00cc00;
  border-radius: 3px;
  background: #222222;
  color: #00cc00;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1;
  padding: 0.75rem 2.25rem; }
  button:hover, button:active, button:focus,
  input[type="button"]:hover,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:hover,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:hover,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    border-color: #009900;
    color: #009900; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  border: 2px solid;
  border-color: #00cc00;
  border-radius: 3px;
  color: #FFFFFF;
  line-height: 1.5;
  background: inherit;
  margin: 0 0 0.999rem;
  padding: 3px; }
  @media (min-width: 620px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"],
    textarea {
      font-size: 20px;
      font-size: 1.25rem; } }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #FFFFFF; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px;
  margin: 0 0 0.999rem;
  width: 60%; }
  @media (min-width: 620px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"] {
      font-size: 20px;
      font-size: 1.25rem; } }

textarea {
  padding-left: 3px;
  width: 100%;
  margin: 0 0 0.999rem; }
  @media (min-width: 620px) {
    textarea {
      font-size: 20px;
